/* SIDEBAR LAYOUT */
.sidebar-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: var(--bg);
  color: var(--text);
  font-family: "Google Sans", sans-serif;
  transition: background 0.3s, color 0.3s;
  border-radius: 18px;
  overflow: hidden;
}

/* THEMES */
body[data-theme="light"] {
  --bg: rgba(240, 244, 249, 0.95);
  --card-bg: #ffffff;
  --text: #1f1f1f;
  --border: #e0e0e0;
  --shadow: 0 4px 16px rgba(0,0,0,0.08);
  --inner-shadow: inset 0 2px 4px 0 rgba(0,0,0,0.05);
  --hover-bg: #e8f0fe;
  --primary-color: #1a73e8;
}

body[data-theme="dark"] {
  --bg: rgba(19, 19, 20, 0.95);
  --card-bg: #1e1e1e;
  --text: #e8eaed;
  --border: #3c4043;
  --shadow: 0 4px 16px rgba(0,0,0,0.16);
  --inner-shadow: inset 0 2px 4px 0 rgba(0,0,0,0.1);
  --hover-bg: #282a2d;
  --primary-color: #8ab4f8;
}

/* HEADER */
.header-card {
  padding: 8px 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: transparent;
  border-bottom: 1px solid var(--border);
}

.title-wrapper {
  display: flex;
  flex-direction: column;
}

.title {
  font-size: 1rem;
  font-weight: 500;
}

.subtitle {
  font-size: 0.75rem;
  opacity: 0.7;
}

.header-actions {
  display: flex;
  gap: 8px;
}

.icon-btn {
  background: transparent;
  color: var(--text);
  border: none;
  padding: 6px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.2s;
}

.icon-btn:hover {
  background: var(--hover-bg);
}

/* MAIN SCROLL AREA */
.content-area {
  flex-grow: 1;
  overflow-y: auto;
  padding: 0 16px 16px 16px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* CARDS */
.card {
  background: var(--card-bg);
  border: none;
  padding: 16px;
  border-radius: 12px;
  box-shadow: var(--shadow);
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
}

.card-title {
  margin-bottom: 0;
  font-weight: 500;
  font-size: 0.9rem;
  color: var(--primary-color);
}

.upload-btn {
  background: var(--hover-bg);
  color: var(--primary-color);
  border: 1px solid var(--border);
  padding: 6px 12px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 0.8rem;
  font-weight: 500;
  transition: background 0.2s;
}

.upload-btn:hover {
  background: var(--border);
}

/* TABS */
.tab-card {
  padding: 0;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.tab-header {
  display: flex;
  border-bottom: 1px solid var(--border);
}

.tab-btn {
  flex: 1;
  padding: 12px;
  background: transparent;
  border: none;
  color: var(--text);
  cursor: pointer;
  font-size: 0.9rem;
  opacity: 0.7;
  transition: background 0.2s, opacity 0.2s;
  border-bottom: 2px solid transparent;
}

.tab-btn.active {
  opacity: 1;
  border-bottom: 2px solid var(--primary-color);
}

.tab-content {
  padding: 16px;
  flex-grow: 1;
  overflow-y: auto;
}

/* BUTTON */
.action-btn {
  width: 100%;
  padding: 10px;
  background: #007bff;
  color: white;
  border-radius: 8px;
  border: none;
  margin-bottom: 8px;
}

.uploaded {
  font-size: 0.85rem;
  opacity: 0.7;
}

/* TEXTAREAS */
.text-box {
  width: 100%;
  height: 120px;
  background: transparent;
  border: none;
  color: var(--text);
  padding: 0;
  resize: vertical;
  font-size: 0.9rem;
}

.text-box:focus {
  outline: none;
}

/* RESULT BOX */
.result-box {
  min-height: 150px;
  max-height: 300px;
  overflow-y: auto;
  background: transparent;
  padding: 0;
  white-space: pre-wrap;
  font-size: 0.9rem;
  line-height: 1.6;
}

/* --- CHATGPT STYLE CHAT BAR --- */

.chat-bar {
  display: flex;
  align-items: center;
  padding: 12px 16px;
  gap: 10px;
  background: var(--card-bg);
  border-top: 1px solid var(--border);
  border-radius: 24px;
  margin: 0 16px 16px 16px;
  box-shadow: var(--shadow);
}

/* Rounded pill input */
.chat-input {
  flex-grow: 1;
  padding: 0;
  font-size: 0.9rem;
  border: none;
  background: transparent;
  color: var(--text);
  outline: none;
}

.chat-input:focus {
  border-color: transparent;
}

/* Circular Send Button (floating style) */
.chat-send {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  background: var(--primary-color);
  color: var(--bg);
  font-size: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.2s, transform 0.1s;
}

.chat-send:hover {
  opacity: 0.9;
}

.chat-send:active {
  transform: scale(0.95);
}
/* Modern, thin scrollbar */
*::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

*::-webkit-scrollbar-track {
  background: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  transition: background-color 0.2s;
}

body[data-theme="dark"] *::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.2);
}

*::-webkit-scrollbar-thumb:hover {
  background-color: rgba(0, 0, 0, 0.4);
}

body[data-theme="dark"] *::-webkit-scrollbar-thumb:hover {
  background-color: rgba(255, 255, 255, 0.4);
}
/* Dark mode scrollbar */
.leet-container[data-theme="dark"] *::-webkit-scrollbar-track {
  background: #333;
}

.leet-container[data-theme="dark"] *::-webkit-scrollbar-thumb {
  background-color: #555;
}

.leet-container[data-theme="dark"] *::-webkit-scrollbar-thumb:hover {
  background-color: #666;
}
.chat-box {
  flex-grow: 1;
  overflow-y: auto;
  padding: 8px;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

/* User bubble */
.chat-bubble.user {
  align-self: flex-end;
  background: var(--button-bg);
  color: var(--button-text);
  padding: 10px 14px;
  border-radius: 14px 14px 0 14px;
  max-width: 80%;
}

/* Bot bubble */
.chat-bubble.bot {
  align-self: flex-start;
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  padding: 10px 14px;
  border-radius: 14px 14px 14px 0;
  max-width: 80%;
  color: var(--text-color);
}

/* Scrollbar fixed for chat */
.chat-box::-webkit-scrollbar {
  width: 6px;
}
.chat-box::-webkit-scrollbar-thumb {
  background: var(--border-color);
  border-radius: 20px;
}
.chat-plus-wrapper {
  position: relative;
}

.chat-plus-btn {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: 1px solid var(--border);
  background: transparent;
  color: var(--text);
  font-size: 18px;
  cursor: pointer;
  transition: background 0.2s;
}

.chat-plus-btn:hover {
  background: var(--hover-bg);
}

.chat-action-menu {
  position: absolute;
  bottom: 48px;
  left: 0;
  background: var(--card-bg);
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 8px;
  display: flex;
  flex-direction: column;
  gap: 6px;
  width: 220px;
  z-index: 50;
  box-shadow: var(--shadow);
}

.menu-item {
  padding: 10px 12px;
  border-radius: 6px;
  background: transparent;
  border: none;
  color: var(--text);
  cursor: pointer;
  text-align: left;
  font-size: 0.9rem;
  transition: background 0.2s;
}

.menu-item:hover {
  background: var(--hover-bg);
}

/* CHAT WINDOW STYLES */
.chat-window {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  max-height: 400px; /* Adjust as needed */
}

.chat-messages {
  flex-grow: 1;
  overflow-y: auto;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 12px;
  min-height: 100px;
}

.chat-message {
  padding: 8px 12px;
  border-radius: 15px;
  max-width: 80%;
  word-wrap: break-word;
  font-size: 0.9rem;
}

.chat-message.user {
  align-self: flex-end;
  background-color: var(--hover-bg);
  color: var(--text);
  border-bottom-right-radius: 4px;
}

.chat-message.ai {
  align-self: flex-start;
  background-color: transparent;
  color: var(--text);
  border-bottom-left-radius: 4px;
}

.sender-label {
  font-weight: bold;
  margin-right: 5px;
}

.no-messages {
  text-align: center;
  color: var(--text);
  opacity: 0.6;
  padding: 20px;
}
